<template>
    <div>

        <h3>This TestKetNoiBan</h3>
        <!-- <div>{{ ds }}</div> -->
        <ul>
            <li v-for="x in ds" :key="x.id">
                {{ x }}
                <button @click="onEdit(x)">Edit</button>
                <button @click="onDel(x)">Del</button>
            </li>
        </ul>


    </div>
</template>
<script>
import fetchClient from '@/fetchClient';
export default {
    components: {
    },
    data(){
        return{
            ds:[],
            sel:{}
        }
    },
    mounted(){
        this.GetAll();
    },
    methods: {
        onEdit(x){
            alert(`on edit `);
        },
        onDel(x){
            alert(`on del `);
        },
        async GetAll() {
            return await fetchClient.get(`/api/Ban/GetAll`)
                .then(res => {
                    //alert(JSON.stringify(res));
                    this.ds=res.data;
                })
                .catch((err) => {
                    if (err.response) {
                        if (err.response.status == 401)
                            alert("Chưa đăng nhập hoặc token hết hạn");
                        else if (err.response.status == 403)
                            alert("Bạn ko có quyền");
                    } else if (err.request) {
                        alert(JSON.stringify(err.request));
                    } else if (err.message) {
                        alert(JSON.stringify(err.message));
                    }
                });
        },

    }
}
</script>
